        p.mb-0 Desde el siglo XX, la OMT reconoce que: el turismo tambin puede abarcar los tiempos de trabajo de personas que tienen que realizar actividades fuera de su entorno habitual y usan los servicios tursticos. A este tipo de turismo se le llama turismo de negocios, que se diferencia del turismo masivo de ocio.
          br
          br
          | El turismo de negocios y eventos incluye la gestin en organizacin, promocin, venta y distribucin de reuniones y eventos, segn la Organizacin Mundial del Turismo; integra tambin este tipo de turismo, entre otros, a congresos, seminarios, conferencias, ferias, convenciones y reuniones de carcter gubernamental, empresarial o social. 
    
