    #t_1_3.titulo-segundo.color-acento-contenido
      h2 1.3.	Clasificacin de los eventos
    p Existen diferentes clases de eventos, entre ellos, se encuentran eventos acadmicos, cuya finalidad es compartir y divulgar informacin sobre un tema de inters en un rea especfica; eventos sociales, reuniones para compartir o celebrar algn acontecimiento especfico (celebraciones, negocios); y eventos de promocin comercial, cuyo objetivo es promocionar a las empresas de cualquier sector. Hacen parte de la actividad empresarial de las organizaciones y, por tanto, de las estrategias de mercadeo. 
    p Dentro de la clasificacin de eventos, se tienen los eventos de tipo social, que son los eventos ms comunes y frecuentes a lo largo de la historia de la humanidad. Le invitamos a ver el siguiente recurso, donde conocer en detalle diferentes elementos de estos eventos: 
