            p.mt-5 Cuando el evento se desarrolla dentro de alguna instalacin, como hoteles, clubes sociales u otros recintos que lo permitan. Se encuentran:
            p.mb-0.mt-4 1. Coctel
            p.mb-0 2. Desayuno, almuerzo o cena de trabajo
            p.mb-0 3. Fiesta
            p.mb-0 4. Festival


          .col-md-7.mt-4
            figure
              img(src='@/assets/curso/Tema1/122.png', alt='Texto que describa la imagen')
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h4 1. Coctel

            ul.lista-ul.mt-5
              li 
                i.fas.fa-check.c2
                | Recepcin social que puede ser de inauguracin o de clausura de un evento o certamen. 
              li 
                i.fas.fa-check.c2
                | Tiempo limitado  (de 2 a 3  horas en promedio).
              li  
                i.fas.fa-check.c2
                | Asistencia controlada y seleccionada.
              li.mt-4 
                i.fas.fa-check.c2
                | Se tiene la oportunidad para dirigir algunas palabras o hacer una presentacin. 
          .col-md-7.mt-4
            figure
              img(src='@/assets/curso/Tema1/123.png', alt='Texto que describa la imagen')
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h4 Caractersticas de un coctel

