                | Se realiza, por lo general, en la tarde o noche.
              li 
                i.fas.fa-check.c2
                | Evento que permite reunir una gran cantidad de personas, con un bajo costo de operacin (en promedio).
              li  
                i.fas.fa-check.c2
                | Los alimentos estn presentados como canaps, fros y calientes, de tamao pequeo, que puedan ser tomados con las manos, pues lo fundamental del coctel es que es un evento a pie.
              li 
                i.fas.fa-check.c2
                | Bebidas alcohlicas y sin alcohol.
              li 
                i.fas.fa-check.c2
                | Decoracin acorde con el evento.
              li 
                i.fas.fa-check.c2
                | Servicio de meseros.
              li
                i.fas.fa-check.c2
                | Mesas adornadas, tipo islas, para proveer de alimentos y bebidas a los invitados.
          .col-md-7.mt-4
            figure
              img(src='@/assets/curso/Tema1/133.png', alt='Texto que describa la imagen')
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h4 2. Desayuno, almuerzo o cena de trabajo

            ul.lista-ul.mt-5
              li 
                i.fas.fa-check.c2
                | Sesin de trabajo donde se espera un producto o resultado final y que se hace al ritmo del consumo de los alimentos.

              li 
