                p Rito social, compartido entre un grupo de personas, donde se marca un cierto acontecimiento a modo de celebracin.  Se integra con una presentacin artstica, baile, <i>shows</i>, etc.


              li 
                i.fas.fa-check.c2
                | Alto costo para su ejecucin.


              li  
                i.fas.fa-check.c2
