              li  
                i.fas.fa-check.c2
                | Tiene el poder de congregar a mucha ms gente, ya que la variedad de los actos y la publicidad que se le da al festival en s son mucho mayores.
              
              li  
                i.fas.fa-check.c2
                | Generalmente, los festivales son de arte, msica y danza.
          .col-md-7.mt-4
            figure
              img(src='@/assets/curso/Tema1/16.png', alt='Texto que describa la imagen')
    p Continuando con esta clasificacin, se tienen los eventos acadmicos, que son aquellas reuniones de orden formal, en las que se discuten o abordan temas de inters profesional. En el siguiente recurso, podr reconocer los diferentes tipos de eventos que hacen parte de esta tipologa: 
