      div(x="35%" y="19%" tooltip="Conferencia" )
        .row.justify-content-center
          .col-lg-6
            h5 1. Conferencia

            p.mb-0.mt-4 Es una reunin en la cual se invita, por lo general, a ponentes destacados en una especialidad. 
            p.mb-0 Se usan herramientas visuales, como grficos estadsticos, fotografas, videos, etc.
            p.mb-0 El objetivo de una conferencia es convencer al auditorio sobre un determinado tema. 
            p En una exposicin, por el contrario, solo se entrega informacin sobre un tema.
            p.mb-0 <strong>Caractersticas de una conferencia</strong>
            p Se distingue por no ser espontnea, sino responder a una planificacin:
            ul.lista-ul
              li 
                i.fas.fa-check.c2
                | Proyectar el nmero de conferencistas.
 
              li 
                i.fas.fa-check.c2
                | La cantidad de asistentes.

              li  
                i.fas.fa-check.c2
                | El da y hora en que se realizar.
              li  
                i.fas.fa-check.c2
                |El sitio donde se llevar a cabo y los temas que se tratarn.
            p.mb-0 El conferencista tambin deber estructurar su discurso.
            p.mb-0 Por lo general, se abre una ronda de preguntas por parte del pblico.
            p.mb-0 Una conferencia no deber sobrepasar los 30 o los 45 minutos, a lo sumo, una hora.
          .col-lg-5.mt-5
            img(src='@/assets/curso/Tema1/37.png', alt='Texto que describa la imagen')
      div(x="32%" y="47%" tooltip="Simposio" )
        .row.justify-content-center
          .col-lg-6
            h5 2. Simposio
            p.mt-4 Reunin de expertos, se expone y desarrolla un tema en forma completa y ms detallada que en una conferencia.
            p Los especialistas exponen durante 15 a 20 minutos. 
            p Duracin mnima de 6 horas (dura ms que una conferencia).
            p Mnimo 20 participantes y de 3 a 6 expertos.
            p Un coordinador resume las ideas principales.
            p El auditorio formula preguntas y dudas que los expertos aclaran y responden.

          .col-lg-5.mt-5
            img(src='@/assets/curso/Tema1/188.png', alt='Texto que describa la imagen')
      div(x="38%" y="80%" tooltip="Cumbre" )
        .row.justify-content-center
          .col-lg-6
            h5 3. Cumbre
            p.mt-4 Reuniones donde participan personas muy importantes (presidentes, ministros, secretarios generales, directores, etc.) que ocupan cargos de gran responsabilidad y visibilidad de diversos Estados u organismos internacionales, donde se toman decisiones importantes.
            p Duracin: mnimo un da, mximo 3. 
            p Los participantes son los representantes de naciones u organizaciones internacionales importantes. 
          .col-lg-5.mt-5
            img(src='@/assets/curso/Tema1/19.png', alt='Texto que describa la imagen')
      div(x="66%" y="19%" tooltip="Congreso" )
        .row.justify-content-center
          .col-lg-6
            h5 4. Congreso

            p.mt-4 Reunin generalmente peridica, donde se exponen y debaten mltiples ponencias. Asisten personas con un alto nivel profesional, intereses comunes.

            p Presentaciones de nuevos avances o descubrimientos en determinadas materias.

            p Debe al menos durar de 3 a 5 das, para poder llamarle congreso.

          .col-lg-5.mt-5
            img(src='@/assets/curso/Tema1/200.png', alt='Texto que describa la imagen')
      div(x="69%" y="47%" tooltip="Foro" )
        .row.justify-content-center
          .col-lg-6
            h5 5. Foro
            p.mt-4 Es una reunin enfocada a propiciar el intercambio de opiniones y conceptos por parte los integrantes del evento.
            p Se discute o responde a temas especficos de sus ponencias con el auditorio participante.
            p Un moderador controla y gua las preguntas realizadas por el pblico hacia los ponentes. Se utiliza un <strong>lenguaje informal y coloquial</strong>, distribuyendo de forma igualitaria el uso de la palabra.


          .col-lg-5.mt-5
            img(src='@/assets/curso/Tema1/211.png', alt='Texto que describa la imagen')
      div(x="66%" y="80%" tooltip="Debate" )
        .row.justify-content-center
          .col-lg-6
            h5 6. Debate
            p.mt-4 El debate comienza con una pregunta concreta referida a un tema de inters, donde cada persona puede participar de forma espontnea, imprevisible y heterognea.
            p Discusin de dos grupos de personas con posiciones contrapuestas, que se renen para llegar a una solucin de algo. 
          .col-lg-5.mt-5
            img(src='@/assets/curso/Tema1/07.png', alt='Texto que describa la imagen')
    p Se cierra esta clasificacin con los eventos comerciales o de promocin, que son reuniones organizadas por empresarios o instituciones con la finalidad de dar a conocer sus marcas, productos o servicios. Para conocer los tipos de eventos que hacen parte de esta tipologa, le invitamos a ver el siguiente recurso:
