    p En la primera fase, que se conoce como pre-evento, se desarrollan las acciones que ayudan a posicionar una imagen nica del evento en la mente y el corazn del pblico objetivo, por ello, es importante crear tcticas consistentes, que respalden la estrategia de comunicacin de este. Esta primera fase tiene dos fases, que son la planeacin y la organizacin del evento.
    p La planeacin es la base del evento, esta fase consiste en formular planes que servirn de gua para el accionar del evento; esto implica establecer objetivos y acciones para alcanzar dichos objetivos. En el siguiente recurso, podr encontrar todos los elementos que hacen parte de la fase de planeacin:
