              p.mt-5 Se eligen los recursos humanos, materiales, tecnolgicos.

              ul.lista-ul
                li
                  i.fas.fa-check.c2
                  p <strong>Recurso humano:</strong> <br>Tcnicos sonido y video, agentes de seguridad, logstica, etc.
                li
