                  p.mb-0 Para la planeacin del montaje debes considerar que el saln sea suficientemente grande para montar un servicio de coffee break y que cuente con: salidas de evacuacin, pasillos para moverse libremente y espacios adecuados para personas con discapacidad.
                li.mb-0
                  i.fas.fa-check.c2
                  p.mb-0 Los eventos al aire libre, instalaciones deportivas, monumentos histricos requieren permisos especiales de las alcaldas locales, o de la institucin gubernamental que gestione ese espacio.
