    p Ya abordada la fase de pre-evento, junto con las dos fases que la conforman, es importante detallar lo que ocurre en las fases de evento y pos-evento. Teniendo en cuenta que en el evento es donde se van a ejecutar todas las actividades que se planificaron durante el pre-evento. 
    .bloque-texto-g.color-acento-contenido.p-3.p-sm-4.p-md-5
      .bloque-texto-g__img(
        :style="{'background-image': `url(${require('@/assets/curso/Tema1/img39.png')})`}"
      )
      .bloque-texto-g__texto.p-5
        p.mb-0 Va a consistir en la reunin de todas las personas involucradas en el mismo, en el lugar y hora estimada, guiadas por la programacin que el comit organizador prepar como evento. En este sentido, se aconseja realizar un ensayo de todo el evento al menos con 24 horas de anticipacin, para asegurar que todo est en orden y, en caso de que algo falle, poder corregirlo.
    
    .row.mt-5
      .col-lg-6
        p Una vez finaliza el evento, inicia la fase de pos-evento, que se basa en dos momentos importantes, el primero, que apunta a la recopilacin y sistematizacin de la informacin pertinente para el evento, y la evaluacin del mismo.
        .row
          .col-lg-12
            .cajon.color-primario.p-4
              p.mb-0 Recopilar y sistematizar la informacin es una tarea que inicia justo al culminar el evento, para ello, las comisiones debern entregar un informe de sus actividades. Dicha informacin deber guardarse en forma ordenada para facilitar futuras consultas. Con lo anterior, se procede a realizar la evaluacin del evento, accin que consiste en medir si lo planificado se cumpli en el momento oportuno. Se recomienda que se enve a los asistentes una encuesta de opiniones para saber cmo les ha resultado el evento y si se ha conseguido el objetivo del mismo. 
              p.mb-0 Dicha evaluacin tambin se debe consignar en el informe.

        
      .col-lg-6.mt-2
        img(src='@/assets/curso/Tema1/img40.png' alt='AvatarTop')
    p.mt-4 Es importante cerrar el evento con los respectivos agradecimientos; es decir, se deben enviar cartas o un correo electrnico de agradecimiento a todas aquellas personas que hicieron posible que el evento se desarrollara conforme con lo previsto (patrocinadores, ponentes, etc.).
    separador
    #t_1_6.titulo-segundo.color-acento-contenido
      h2 1.6.	Montajes de los eventos
    p Se reconoce como montaje del evento al proceso que se utiliza para disear los planos y secuencias de un <strong>evento</strong> o actividad, de forma que los asistentes perciban la integracin armnica de todo lo que haga parte del mismo. Para entender mejor este tema, le invitamos a ver el siguiente recurso sobre los tipos de montajes de eventos: 
    .tarjeta.tarjeta--azul.p-4.mb-5.fon22   
      SlyderA.mt-5.aq3(tipo="b")
        .row.justify-content-center
          .col-md-5.mb-4.mb-md-0
            h4 Tipo escuela
            p.mt-4 Utilizado para realizar actividades de tipo acadmico,  pero con pocas personas.
            p.mt-4.mb-0 <strong>Su escenografa:</strong>
            p Una tarima con mesa principal y, frente a esta, mesas rectangulares con capacidad para alrededor de cuatro a seis personas; se dejan espacios entre cada mesa para que las personas puedan caminar sin problema.
            p.mt-4.mb-0 <strong>Beneficio:</strong>
            ul.lista-ul.mb-0
              li.mb-0
                i.fas.fa-check.c2
                p.mb-0 Las personas pueden colocar material de apoyo o aparatos tecnolgicos en la mesa para tomar notas. 
              li.mb-0
                i.fas.fa-check.c2
                p Facilita a los participantes ver claramente lo que se proyecte en la pantalla o lo que exponga el ponente.
            p.mb-0 <strong>Requerimientos:</strong>
            p.mb-0 Saln grande y un nmero de participantes pequeo.
          .col-md-6.mt-4
            figure
              img(src='@/assets/curso/Tema1/img41.png', alt='Texto que describa la imagen')
        .row.justify-content-center
          .col-md-5.mb-4.mb-md-0
            h4 Tipo auditorio
            p.mt-4 Se usa cuando se tienen actividades acadmicas con audiencias muy grandes, como, por ejemplo, asambleas, reuniones de funcionarios, asociaciones, entre otros. 
            p.mt-4.mb-0 <strong>Su escenografa:</strong>
            p Tiene filas rectas de asientos con un pasillo en el centro o dos pasillos laterales. Los asientos miran hacia el rea del ponente. 
            p.mt-4.mb-0 <strong>Beneficio:</strong>
            ul.lista-ul.mb-0
              li.mb-0
                i.fas.fa-check.c2
                p.mb-0 Es el montaje ms fcil de hacer por el personal encargado y es el ms comn de los estilos de montaje.
              li.mb-0
                i.fas.fa-check.c2
                p El expositor no tiene problemas para ver a los miembros del auditorio. 
          .col-md-6.mt-4
            figure
              img(src='@/assets/curso/Tema1/img42.png', alt='Texto que describa la imagen')
        .row.justify-content-center
          .col-md-5.mb-4.mb-md-0
            h4 Tipo "U"
            p.mt-4 Se utiliza para realizar actividades acadmicas o sociales para pocas personas (16 personas o menos). Si el grupo es mayor de 20 personas es preferible buscar otro estilo de disposicin del saln.

            p.mt-4.mb-0 <strong>Su escenografa:</strong>
            p Incluye mesas y asientos colocados en tres lados del saln con un cuadrado vaco en el centro. 

            p.mt-4.mb-0 <strong>Beneficio:</strong>
            p Todos pueden ver al dictante y tambin interactuar entre ellos. Facilita la discusin y promueve tanto la comunicacin interpersonal como la no verbal. 
            p.mt-4.mb-0 <strong>Recomendacin:</strong>
            p Se recomienda para ruedas de prensa o eventos donde asistirn medios de comunicacin.

          .col-md-6.mt-4
            figure
              img(src='@/assets/curso/Tema1/img43.png', alt='Texto que describa la imagen')
        .row.justify-content-center
          .col-md-5.mb-4.mb-md-0
            h4 Tipo mesa imperial
            ul.lista-ul.mb-0.mt-4
              li.mb-0
                i.fas.fa-check.c2
                p.mb-0 Se usa, sobre todo, para celebraciones protocolarias, almuerzos o cenas de negocios, pues la integran gran cantidad de comensales, a partir de 50 personas, generalmente. 

              li.mb-0
                i.fas.fa-check.c2
                p Este montaje es usual emplearlo en palacios o en ciertas instituciones del Estado.
              li.mb-0
                i.fas.fa-check.c2
                p No hay ponencias, se usa para celebrar, ms que todo.

            p.mt-4.mb-0 <strong>Su escenografa:</strong>
            p Mesa cuadrada, sin hueco en el centro.<br>Se utiliza en eventos formales.
            p.mt-4.mb-0 <strong>Beneficio:</strong>
            p Mayor interaccin entre los participantes.

          .col-md-6.mt-4
            figure
              img(src='@/assets/curso/Tema1/img44.png', alt='Texto que describa la imagen')
        .row.justify-content-center
          .col-md-5.mb-4.mb-md-0
            h4 Tipo banquete
            p.mt-4 Aunque su nombre es de banquete, no quiere decir que usted tiene que servir comida para disponer un saln de conferencias de esta manera. <br>Este tipo de estilo promueve trabajo en equipo y discusin en grupo. Pueden ser eventos sociales como desayunos, almuerzos o cenas, bodas, quince aos, aniversarios, entre otros, o tambin pueden ser reuniones de trabajo.
            ul.lista-ul.mb-0.mt-4
              li.mb-0
                i.fas.fa-check.c2
                p.mb-0 <strong>Su escenografa:</strong><br>Mesas redondas o rectangulares  montadas para 6 a 10 personas.<br>Se pueden iluminar para generar decoracin.
              li.mb-0
                i.fas.fa-check.c2
                p <strong>Desventajas:</strong><br>La mitad de los participantes tendrn que girar sus asientos en la direccin opuesta para ver al ponente, novios, o quinceaera, segn sea el caso.<br>Tambin ser dificultoso para algunos en el auditorio poder ver adecuadamente las ayudas visuales. 

          .col-md-6.mt-4
            figure
              img(src='@/assets/curso/Tema1/img45.png', alt='Texto que describa la imagen')
        .row.justify-content-center
          .col-md-5.mb-4.mb-md-0
            h4 Tipo coctel
            p.mt-4 Es uno de los montajes ms prcticos cuando se ofrece una alimentacin ligera, precisamente para los cocteles, refrigerios, entre otros. 
            p.mt-4 Su uso favorece cuando hay muchos comensales y no se cuenta con un espacio amplio para atenderlos a todos; el montaje tipo coctel viene a resolver esta situacin.
            p.mt-4  La ventaja es que ahorra espacio; sin embargo, se debe recordar que es para actividades que no duren ms de una hora, debido a que los invitados estarn de pie.


          .col-md-6.mt-4
            figure
              img(src='@/assets/curso/Tema1/img46.png', alt='Texto que describa la imagen')
        .row.justify-content-center
          .col-md-5.mb-4.mb-md-0
            h4 Tipo lounge
            p.mt-4 Hoy en da, se utiliza mucho para eventos sociales con un ambiente relajado. Se usa para actividades de celebracin. Usted podr sentarse a beber algo, conversar y escuchar msica. 
            p.mb-0 Su escenografa:
            ul.lista-ul.mb-0.mt-4
              li.mb-0
                i.fas.fa-check.c2
                p.mb-0 Se caracteriza por emplear cmodos sillones para dos personas o individuales, puffs y mesas iluminadas, etc. Todo con un estilo original y, en muchos casos, minimalista.<br>Uso de luces y velas como parte de la decoracin.
              li.mb-0
                i.fas.fa-check.c2
                p Beneficios:<br>El ambiente relajado y de esparcimiento que se genera.<br>Mucha interaccin entre participantes.

            p.mt-4.mb-0 <strong>Desventajas:</strong>
            p No es recomendable para eventos acadmicos.


          .col-md-6.mt-4
            figure
              img(src='@/assets/curso/Tema1/img47.png', alt='Texto que describa la imagen')
        .row.justify-content-center
          .col-md-5.mb-4.mb-md-0
            h4 Tipo feria
            ul.lista-ul.mb-0.mt-4
              li
                i.fas.fa-check.c2
                p Este montaje se usa para la exposicin de cualquier tipo de mercancas en ferias y eventos.
              li
                i.fas.fa-check.c2
                p Los toldos dan un toque de alegra e identidad cultural. 
              li
                i.fas.fa-check.c2
                p Se pueden usar presentaciones modernas, pero sin perder su tradicionalismo.
              li
                i.fas.fa-check.c2
                p Se usan en diferentes colores y telas impermeables.
          .col-md-6.mt-4
            figure
              img(src='@/assets/curso/Tema1/img48.png', alt='Texto que describa la imagen')
    separador
    #t_1_7.titulo-segundo.color-acento-contenido
      h2 1.7.	Recursos para la organizacin de eventos 
    p A la hora de iniciar la organizacin de un evento, sin duda, uno de los factores ms importantes son los recursos con los se puede contar para la organizacin del mismo. Siendo consecuente con ello, en la siguiente tabla encontrar algunos recursos humanos, materiales y fsicos con los que se debe contar:
    .titulo-sexto.color-acento-contenido.mt-5
      h5 Tabla 2
      span  Recursos para la organizacin de eventos
    .tabla-a.color-acento-contenido.mb-5 
      table
        thead
          tr
            th.aqw Recursos humanos
            th Recursos materiales
            th Recursos fsicos
        tbody
          tr
            td 
              p Asigne responsabilidades y establezca necesidades.
              ul.lista-ul
                li 
                  i.fas.fa-check.c2
                  | Organizador general de la actividad.
                li 
                  i.fas.fa-check.c2
                  | Moderador: conductor del acto.
                li 
                  i.fas.fa-check.c2
