      h2 1.8.	Espacios para la realizacin de eventos
    p El espacio en el que se lleva a cabo un evento juega un papel importante, pues es el plano en el que los asistentes del mismo viven la experiencia, que es el principal objetivo de este tipo de organizaciones. Conozca algunos de estos espacios: 
    .tarjeta.tarjeta--azul.p-4.mb-5.fon11   
      SlyderA.mt-5.aq2(tipo="b")
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h4 Eleccin de un espacio
            p.mt-5 Elegir un espacio para realizar un evento no es tarea fcil, ms an porque la sociedad pide personalizacin, innovacin y entretenimiento. Es por ello que en los ltimos aos han proliferado los espacios para eventos diferentes, adaptados a las peticiones de los visitantes. 
            p Muchos propietarios de estos espacios diferentes los alquilan para eventos, rodajes o sesiones de fotos. Ya no es necesario caer en el clsico concepto de hotel o sala de conferencias: 
            p.text-center <strong>Hacer que la mezcla entre el espacio y el objetivo del evento sean acordes.</strong>
          .col-md-7.mt-4
            figure
              img(src='@/assets/curso/Tema1/img51.png', alt='Texto que describa la imagen')    
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h4 Espacios para eventos rurales y naturales
            p.mt-5 Casas rurales o rodeadas de naturaleza tienen una gran demanda para realizar todo tipo de eventos; desde bodas u otros eventos sociales hasta eventos corporativos, est muy de moda celebrar cualquier acontecimiento en lugares donde la naturaleza es el marco protagonista.

          .col-md-7.mt-4
            figure
              img(src='@/assets/curso/Tema1/img52.png', alt='Texto que describa la imagen')   
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h4 Palacios y casas con historia en los centros de la ciudad
            p.mt-4 Los propietarios de este tipo de espacios cuidan mucho los detalles y mantienen la grandiosidad de otras pocas: decoraciones barrocas, estilos neoclsicos, espacios de carcter victoriano.
            p Desde eventos corporativos hasta sociales, pasarelas, <i>showrooms</i>. Este tipo de espacios tienen un encanto especial que puede ser muy adecuado en algunos tipos de acontecimientos en concreto.
          .col-md-7.mt-4
            figure
              img(src='@/assets/curso/Tema1/img53.png', alt='Texto que describa la imagen')   
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h4 Almacenes, fbricas y bodegas industriales
            p.mt-4 La decoracin de estilo industrial est de moda. Ambientes creados en naves industriales, fbricas, almacenes o simplemente en salas con este tipo de decoracin son un xito asegurado.

            p Los beneficios de este tipo de espacios estn principalmente relacionados con el tamao: capacidad para muchos visitantes y muchos elementos. 

          .col-md-7.mt-4
            figure
              img(src='@/assets/curso/Tema1/img54.png', alt='Texto que describa la imagen')   
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h4 <i>Lofts</i>, ticos y azoteas privadas
            p.mt-4 Si necesita un espacio moderno, en el centro de la ciudad y para celebrar un evento con cierto aire desenfadado, debera aadir a la lista las azoteas, terrazas, ticos y <i>lofts</i> privados. 
            p Son ideales para acontecimientos en los que el nmero de asistentes no es muy grande. Si adems el espacio tiene buenas vistas, el evento estar a la altura de todos.
          .col-md-7.mt-4
            figure
              img(src='@/assets/curso/Tema1/img55.png', alt='Texto que describa la imagen')
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h4 Espacios flexibles para <i>branding</i>
            p.mt-4 Desde hace unos aos, y probablemente debido a la crisis, muchos negocios <strong>han optado por usar locales, calles, casas, etc., durante un tiempo limitado para promocionarlo vender sus productos o servicios.</strong> Y la experiencia es siempre positiva.

          .col-md-7.mt-4
            figure
              img(src='@/assets/curso/Tema1/img56.png', alt='Texto que describa la imagen')   
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h4 Gastroespacios
            p.mt-4 En <strong>espacios acadmicos - sociales:</strong>
            p Con una cocina altamente equipada, suelen incluir tambin amplios comedores para recibir a invitados y realizar eventos, y tambin disponer de mesas de trabajo para grupos donde realizar talleres, catas, celebraciones.
          .col-md-7.mt-4
            figure
              img(src='@/assets/curso/Tema1/img57.png', alt='Texto que describa la imagen')  
        .row.justify-content-center
          .col-md-4.mb-4.mb-md-0
            h4 En restaurante - bar
            p.mt-4 Establecimiento comercial donde se combina la gastronoma con los eventos.

          .col-md-7.mt-4
            figure
              img(src='@/assets/curso/Tema1/img58.png', alt='Texto que describa la imagen')  
