    #t_1_9.titulo-segundo.color-acento-contenido
      h2 1.9.	Tendencias en eventos
    p La demanda actual en el segmento turstico de eventos exige evolucin constante de la oferta. Las empresas MICE se reinventan permanentemente, ajustando sus propuestas a estas nuevas exigencias en el mercado de eventos, creando experiencias nicas que motivan al visitante a explorar el destino ms all del evento. 
    p Observe el siguiente recurso, donde se presentan algunas tendencias que se presentan hoy da:
    .row
      .col-lg-6
        .row.mb-5
          .col-md-12.mb-5.mb-md-0
            LineaTiempoD.color-acento-botones
              p(numero="1" titulo="Eventos digitales") Es similar a un evento tradicional; marca la diferencia al ser un evento en lnea y los asistentes se renen desde lugares diversos, a travs de una plataforma. Las plataformas ms usadas en eventos digitales son: Zoom, Meet, Webex, etc. Presenta algunas ventajas, como, por ejemplo: al organizador del evento le proporciona la alternativa de ampliar el nmero de participantes y ponentes que pueden estar ubicados en diferentes partes del mundo o que presentan incompatibilidad de horario. Desde la perspectiva del invitado, el formato de evento <i>online</i> le brinda la oportunidad de pagar un valor ms reducido, estar en cualquier parte del mundo y, en ocasiones, ver el evento en horarios distintos al estipulado originalmente por la empresa (esto cuando se deja grabado).
              p(numero="2" titulo="Eventos hbridos") Su caracterstica principal se encuentra en la interaccin y participacin incrementada al combinar acciones fsicas o materiales con la virtualidad. Se realiza con presencia fsica de los actores en diferentes sedes que estn, a su vez, enlazadas va <i>streaming</i>, posibilitando a los asistentes una retransmisin en vivo, entregando experiencias reales sincrnicas. En el espacio fsico, normalmente asiste un reducido nmero de asistentes, y en la parte virtual, se usan plataformas audiovisuales y tecnolgicas (videoconferencias en <i>streaming, chats,</i> etc.) a travs de las cuales los asistentes pueden participar en el evento desde sus casas o ciudades de origen.<br>Cuenta con varias ventajas: ahorro en tiempo y gastos que se derivan de los traslados de asistentes y ponentes; la audiencia puede ser ms grande; mejora el impacto en redes sociales y tambin posibilita el establecimiento de indicadores de medicin de la trascendencia del evento y satisfaccin de los asistentes.
              p(numero="3" titulo="Eventos en escenarios 3d con la personificacin de avatares") Se usa una rplica de las fisonomas del rostro de la persona transformada a un avatar en 3D, es digital, se convierte en una figura digital capaz de interactuar de manera inmersiva en escenarios virtuales.
              p(numero="4" titulo="Eventos sostenibles") Acciones amigables con el medio ambiente es la consigna mundial hoy, razn por la cual, muchas empresas se enfocan en eventos que minimicen la afectacin al ecosistema, promocionando la produccin del lugar y la conexin con la naturaleza. Algunas medidas que se vienen utilizando son: ofrecimiento de medios de transporte menos contaminantes, el ajuste de las cantidades de alimentos sobrantes en comidas y cenas, etc. 
              p(numero="5" titulo="<i>Small meeting</i>") Consiste en organizar pequeos espacios y grupos de trabajo observando los protocolos de bioseguridad. Sus usuarios valoran el trabajo en equipo y refuerzan vnculos personales y profesionales con clientes, proveedores y colaboradores.          
      .col-lg-6
        img(src='@/assets/curso/Tema1/img60.png', alt='Texto que describa la imagen')
