    #t_1_10.titulo-segundo.color-acento-contenido
      h2 1.10.	Formatos de eventos <i>online</i>
    p Los formatos <i>online</i> son muy flexibles y permiten la adaptacin de un formato de evento presencial como concierto, conferencia, congreso, feria, espectculos y dems para su realizacin. Le invitamos a ver el siguiente recurso, en el que se explican algunos de ellos: 
    .row
      .col-lg-6
        img(src='@/assets/curso/Tema1/img61.png', alt='Texto que describa la imagen')
      .col-lg-6.mt-2
        AcordionA(tipo="b" clase-tarjeta="tarjeta tarjeta--azul1")
          .row(titulo="Cumbre virtual")
            .col-md-12.mb-4.mb-md-0
              p El formato est diseado en funcin del <i>marketing</i>; los ponentes presentan informacin especfica tendiente a varios objetivos: captacin de clientes, fidelizacin de los mismos, construccin de imagen y marca, etc., generando nuevos contenidos para su audiencia. El promedio de oradores es de 30 y su duracin vara entre 3 y 10 das.
          .row(titulo="Conferencias <i>online</i>")
            .col-md-12.mb-4.mb-md-0
              p Sus oradores y temticas son variadas, se realiza en vivo y cuenta con reas virtuales de relajacin, reas para la prctica de <i>networking</i> y reas de entretenimiento.
          .row(titulo="<i>Webinar</i>")
            .col-md-12.mb-4.mb-md-0
              p Conferencia, taller o seminario virtual con contenido educativo; su principal diferencia radica en el nivel de participacin de los asistentes, ya que, en este tipo de evento, los participantes y el conferenciante tienen un nivel de interactividad mayor que en los otros eventos acadmicos.
          .row(titulo="Talleres <i>online</i>")
            .col-md-12.mb-4.mb-md-0
              p Se brinda entrenamiento prctico a los empleados de una organizacin. Pueden durar entre 30 minutos y 3 horas; se requiere una duracin corta y eliminar barreras de acceso para las personas que estn trabajando.
          .row(titulo="Festivales <i>online</i>")
            .col-md-12.mb-4.mb-md-0
              p Combina contenido con experiencias, es decir, existe un programa con una seccin acadmica que puede contener notas claves, talleres, conferencias, etc., y una seccin social, con experiencias que lleven a los participantes al disfrute mientras interactan con otros asistentes. Este tipo de eventos usualmente integran el entretenimiento y la interaccin desde la experiencia, esto puede incluir sesiones de baile, de yoga, meditaciones, discusiones grupales, etc.

    .row
      .col-lg-6
        AcordionA(tipo="b" clase-tarjeta="tarjeta tarjeta--azul1")
          .row(titulo="Chats <i>online fireside</i> (caseros)")
            .col-md-12.mb-4.mb-md-0
              p Formato caracterizado por facilitar la cercana entre asistentes y orador en sesiones privadas y que es considerado como un evento. Tambin puede tener su origen como sesin complementaria en una cumbre o conferencia virtual. 
          .row(titulo="Ferias y exposiciones comerciales virtuales")
            .col-md-12.mb-4.mb-md-0
              p Formato de gran escala en el cual se exponen productos y servicios directamente, con el objetivo de sensibilizar a futuros clientes y fidelizar a los existentes. 
          .row(titulo="<i>Happy hour</i> virtual o <i>afterwork</i>")
            .col-md-12.mb-4.mb-md-0
              p Son eventos cortos, que se pueden realizar en la maana, tarde o noche, fomentando la interaccin entre los participantes en un ambiente ms tranquilo. La idea con estos eventos es que el organizador enve previamente, a la casa de sus invitados, un desayuno, una bebida, o algn tipo de merienda para que pueda ser compartida de manera online con los otros participantes; propicia el relacionamiento y denota afecto y atencin en el bienestar entre oferente e invitados.
          .row(titulo="<i>Tour virtual</i>")
            .col-md-12.mb-4.mb-md-0
              p Es una visita simulada, la cual se construye previamente tomando las imgenes del sitio en 360 grados, facilitando el desplazamiento de una ubicacin a otra. Suele ser aplicado para mostrar una sala de exposiciones de un recinto de eventos, hoteles, espacios icnicos de un destino turstico, un restaurante, un museo, sitios arqueolgicos, etc. Se crean a partir de la tecnologa de realidad virtual, que permite la construccin de lugares artificiales para generar experiencias inmersivas para los usuarios. Cada participante puede desplazarse e interactuar con elementos o mobiliario del lugar que se est mostrando. El <i>tour</i> puede ser un recorrido programado o no; y puede tener libre desplazamiento.
      .col-lg-6
        img(src='@/assets/curso/Tema1/img62.png', alt='Texto que describa la imagen')
    p.mt-4 Estimado aprendiz, para complementar este tema, en el siguiente enlace, encontrar un ejemplo de un <i>tour</i> virtual sobre: La historia del museo y el museo en la historia:
    .row.justify-content-center
      .col-lg-10
        .tarjeta.color-primario.p-3.mb-5
          .row.justify-content-around.align-items-center
            .col-5.col-sm-2.col-lg-1
              img.tam1(src="@/assets/curso/Tema1/ico5.svg")
            .col.mt-4
