      h2 1.11.	 Plataformas para desarrollar eventos virtuales
    p As como existen eventos presenciales, gracias a la globalizacin, que ha permitido la llegada de las tecnologas de la informacin y las comunicaciones  TIC, hoy por hoy existen varias plataformas que se han especializado en ofrecer un servicio de acuerdo con la demanda de un evento de este tipo. Le invitamos a ver el siguiente recurso, donde se presentan las diferentes plataformas empleadas para desarrollar eventos virtuales:
    .row
      .col-lg-6
        img(src='@/assets/curso/Tema1/img71.png', alt='Texto que describa la imagen')
      .col-lg-6.mt-2
        .tarjeta.tarjeta--azul.p-4.mb-5.qaw.sombra
          SlyderA.pp.flecha-blanca(tipo="b")
            .row
              .col-md-12.mb-4.mb-md-0
                .row.justify-content-center
                  .col-lg-5
                    img.baqw(src='@/assets/curso/Tema1/z1.svg', alt='Texto que describa la imagen')

                p.mt-4 Plataforma intuitiva y fcil de usar. Transmite reuniones, conferencias y seminarios <i>web</i> con video, audio y mensajera instantnea. Dispone de versin <i>premium</i>, que posibilita el tener hasta 1.000 participantes y 10.000 espectadores, con herramientas interactivas.
            
            .row
              .col-md-12.mb-4.mb-md-0
                .row.justify-content-center
                  .col-lg-3
                    img.baqw(src='@/assets/curso/Tema1/z2.svg', alt='Texto que describa la imagen')

                p.mt-4 <i>App</i> de videoconferencias desarrollada por la compaa Google Inc, se accede desde buscadores <i>web</i> y dispositivos mviles; las empresas y organizaciones son sus principales usuarios, ya que fue ideada para dinamizar el entorno laboral. Cuenta con una interfaz sencilla, y para poder crear las reuniones, se debe tener activa una cuenta en Google.
            .row
              .col-md-12.mb-4.mb-md-0
                .row.justify-content-center
                  .col-lg-3
                    img.baqw(src='@/assets/curso/Tema1/z3.svg', alt='Texto que describa la imagen')

                p.mt-4 Es un espacio de trabajo diseado para mejorar la comunicacin y colaboracin de los equipos de trabajo de las organizaciones, reforzando las funciones colaborativas de la plataforma en la nube de Microsoft. Cuenta con <i>chats</i> individuales y grupales, conversaciones de equipo, videoconferencias, notas, acceso a contenido compartido, Office Online, Planner, entre otras. 
            .row
              .col-md-12.mb-4.mb-md-0
                .row.justify-content-center
                  .col-lg-8
                    img.baqw(src='@/assets/curso/Tema1/z4.svg', alt='Texto que describa la imagen')

                p.mt-4 Permite tener hasta 6 expositores en vivo, compartir pantalla, y difundir el <i>streaming</i> por medio de Facebook, YouTube y LinkedIn para tener mayor alcance. Videoconferencias, notas, acceso a contenido compartido, Office Online, Planner, entre otras. 
            .row
              .col-md-12.mb-4.mb-md-0
                .row.justify-content-center
                  .col-lg-4
                    img.baqw(src='@/assets/curso/Tema1/z5.svg', alt='Texto que describa la imagen')

                p.mt-4 Se pueden hacer videoconferencias y congresos <i>online</i>. Cuenta con funciones para preguntas y respuestas.
            .row
              .col-md-12.mb-4.mb-md-0
                .row.justify-content-center
                  .col-lg-6
                    img.baqw(src='@/assets/curso/Tema1/z6.svg', alt='Texto que describa la imagen')

                p.mt-4 Puede crear videos en tiempo real, tanto para pequeos negocios como para grandes empresas. Puede usar esta herramienta para realizar demostraciones de productos, reuniones, seminarios, cursos <i>online</i>, capacitaciones o cualquier evento que necesite.




  
