      h2 1.12.	 Mercadeo <i>online</i> para eventos

    p Los eventos virtuales, al igual que los presenciales, deben despertar emociones en los asistentes. Organizadores de eventos, fiestas, conciertos, etc., con muchos aos de experiencia, han comenzado a migrar sus servicios al mundo online, y para ello, ha sido esencial contar con una planificacin y estrategias de mercadeo que logren afianzar sus relaciones con los clientes y conectarlos con los valores de la marca. 
    .row
      .col-lg-6
        img(src='@/assets/curso/Tema1/img70.png', alt='Texto que describa la imagen')
      .col-lg-6.mt-2
        .col-lg-12
          .cajon.color-acento-botones.p-4.mb-4.sombra
            p <strong>Desarrollar estrategias de mercadeo digital para la promocin de eventos no solo es importante en el antes, sino tambin en el durante y en el despus de dichos eventos. Hoy en da, todo suceso pasa por Internet, que es un medio ideal para cualquier tipo de proceso; websites, plataformas digitales, apps han abierto un panorama inmenso al conseguir un mayor impacto y visibilidad para las organizaciones que comprenden que el consumidor moderno es autnomo, gestiona l mismo la informacin, es decir, es un cibernauta.</strong>

        p Dicho esto, queda claro que los medios digitales representan una oportunidad para la formulacin de estrategias de marca en las organizaciones. El mercadeo en este campo realiza mtricas de <i>marketing</i> sincrnicas, interactuando con los consumidores y ajustando la estrategia a partir de los resultados. Esta flexibilidad y dinmica propia del <i>marketing</i> digital resulta, en trminos econmicos, ms viable al comparar con los medios tradicionales de difusin, como radio, televisin o prensa. 
    .row.mt-4
      .col-lg-6
        .row.ppp2.align-items-end
          .col-lg-2.col-4.img-ico
            img(src="@/assets/curso/fondos/icono2.svg")
          .col-lg-8
            h3.mt-2.mb-1 Herramientas de <i>marketing</i> digital.
    
    p.mt-5 El <i>marketing</i> digital se refiere al conjunto de estrategias que se emplean para promocionar, en este caso, los eventos, ya sean presenciales o a travs de alguna plataforma digital; a diferencia del <i>marketing</i> tradicional, este se hace a travs de canales y mtodos que permiten el anlisis de los resultados en tiempo real, tal como lo podr observar en el siguiente recurso, en el que encontrar detalles de la manera como los eventos son socializados a travs de las redes sociales:
    TabsC.color-primario
      .py-3.py-md-4(titulo="<i>Landing page</i>")
        .row
          .col-md-5.mb-4.mb-md-0
            p Son pginas <i>web</i> personalizadas cuya finalidad es atraer visitantes y convertirlos en clientes potenciales presentando informacin de productos y/o servicios especficos. Se llega a ellas cuando el visitante a un sitio <i>web</i> accede a un hipervnculo de los que regularmente se encuentran all. Para el caso de los eventos, el internauta accede a travs de un banner en la red, que lo lleva a conocer rpidamente la informacin del evento como tal. El objetivo de estas <i>landing pages</i> es capturar la atencin del visitante en pocos segundos, por lo tanto, no son sitios donde se compartan grandes detalles del evento, pero s son un abre bocas al mismo, convirtindose en el primer acercamiento de la oferta al prospecto. 

          .col-md-7
            figure
              img(src='@/assets/curso/Tema1/img80.png', alt='Texto que describa la imagen')
      .py-3.py-md-4(titulo="Campaa en redes sociales")
        .row
          .col-md-5.mb-4.mb-md-0
            p Las redes sociales se han convertido  desde hace varios aos en  la va ms rpida y sencilla para la difusin de todo tipo de informacin; cada una de ellas impulsa de forma diferenciada, bien sea a travs de publicidad, <i>lives</i>, videos o mensajes directos.  
          .col-md-7
            .row.colorq.p-3
              .col-lg-3
                img(src='@/assets/curso/fondos/1.png', alt='Texto que describa la imagen')
              .col-lg-3
                img(src='@/assets/curso/fondos/2.png', alt='Texto que describa la imagen')
              .col-lg-3
                img(src='@/assets/curso/fondos/3.png', alt='Texto que describa la imagen')
              .col-lg-3
                img(src='@/assets/curso/fondos/4.png', alt='Texto que describa la imagen')

      .py-3.py-md-4(titulo="<i>Email marketing</i>")
        .row
          .col-md-5.mb-4.mb-md-0
            p Correo electrnico diseado de tal manera que motiva su lectura, redactado en trminos sencillos y cuyo objetivo es cautivar al receptor y propiciar su participacin en un evento. En la operacin del <i>email marketing</i> es importante segmentar la demanda potencial; tener bases de datos clasificadas y organizadas es clave para obtener los logros planteados. 

          .col-md-7
            figure
              img(src='@/assets/curso/Tema1/img82.png', alt='Texto que describa la imagen')
      .py-3.py-md-4(titulo="Distribucin de contenidos")
        .row
          .col-md-5.mb-4.mb-md-0
            p Los contenidos digitales deben tener la facultad de atraer y retener el inters de los visitantes; se constituyen en una herramienta de promocin y venta de productos o servicios al ser integrados al desarrollo de un evento virtual. Existe una gran variedad de formatos para desarrollar los contenidos digitales en torno a la realizacin de un evento; entre ellos estn: <i>blogs</i>, revistas, correos electrnicos, videos, <i>e-books</i>, etc.
          .col-md-7
            figure
              img(src='@/assets/curso/Tema1/img83.png', alt='Texto que describa la imagen')
      .py-3.py-md-4(titulo="<i>Streaming</i> del evento")
        .row
          .col-md-5.mb-4.mb-md-0
            p Es difundir un evento en vivo, con audio y video en directo, a interesados en cualquier lugar del planeta, quienes acceden a travs de una plataforma digital. Presenta caractersticas inmejorables, que contribuyen al buen relacionamiento con clientes, empleados, proveedores y con la comunidad en general; de igual manera, asegura que las personas que no pudieron asistir, queden antojadas para una prxima oportunidad. Para esta estrategia, es necesario tener un equipo tcnico de grabacin robusto y expertos audiovisuales que generen la transmisin.

          .col-md-7
            figure
              img(src='@/assets/curso/Tema1/img84.png', alt='Texto que describa la imagen')
      .py-3.py-md-4(titulo="<i>Influencers</i>")
        .row
          .col-md-5.mb-4.mb-md-0
            p Es una tcnica que est siendo empleada por muchas organizaciones, donde se escoge una persona que tenga reconocimiento en el medio y que est relacionada con la temtica del evento. La idea es que esta persona hable en sus redes sociales sobre la actividad que se est organizando y, de esta manera, impacte favorablemente a la audiencia objetivo.
          .col-md-7
            figure
              img(src='@/assets/curso/Tema1/img85.png', alt='Texto que describa la imagen')

